export default {
	data() {
		return {
			textValue:'',
			textIndex:0,
			user_token:'',
		}
	},
	created(){

	},
	onLoad() {
		var that = this;
		uni.getStorage({
		    key: 'user_token',
		    success: (res)=> {
					console.log(res)
		      that.user_token = res.data;
		    }
		});
		
		// this.getBanner();
		// this.getClass();
	},
	methods:{
		textChange(e){
			console.log(e.detail.value)
			this.textValue = e.detail.value,
			this.textIndex = e.detail.cursor
		},
		submit(){// 获取轮播图
		  if(!this.textValue.length){
				this.$msg('请输反馈内容');
				return false;
			};
			uni.request({
				url: this.$url+'api/suggest/add'+this.user_token+'&content='+this.textValue,
				method: 'post',
				data: {},
				success: res => {
					console.log(res,'轮播');
					if(res.data.code == 10000){
						this.textValue = '';
					}
					this.$msg(res.data.msg)
				},
			})
		},
	},
}
